import axios from 'axios';

const baseURL = window.API_server.API_ADDRESS;// apibase_url
const timeout = window.API_server.TIMEOUT;
const request = axios.create({
  baseURL,
  timeout,
  headers: {
    "Content-Type": "application/json"
  }
});
request.interceptors.request.use(function (config) {
  const operator = vueObj.$store.getters.getOperatorInfo;//vuex  

  console.log("operator infomation", operator);
  if (operator.operator_id !== "") {
    config.headers.Authorization = operator.token;
    config.headers.Operatorid = operator.operator_id;
  } else {
    if (["login,register,check_auth"].find(item => {
      return config.url.indexOf(item)
    })) {
      console.log("");
    } else {
      console.log(", !");
    }
  }

  // 
  return config;

}, function (error) {
  // 
  return Promise.reject(error);
});

request.interceptors.response.use(function (response) {
  const {data, status} = response;
  if (data) {
    if (data["code"] !== 10000) {
      vueObj.ele_notify(data["message"], "warning", data["code"]);
    }
  }

  return response;
}, function (error) {

  const {response: {status}} = error;


  if (status === 401) {
    vueObj.ele_notify("", "error", status);
  } else if (status === 404) {
    vueObj.ele_notify("", "error", status);
  } else if (status >= 500) {
    vueObj.ele_notify("", "error", status);
  }


  // if (response.data) {
  //   switch (response.data["code"]) {
  //     case 10400:
  //       console.log("");
  //       break;
  //     case 10401:
  //       console.log("");
  //       break;
  //     default:
  //       console.log(data.code, data.message);
  //   }
  // }

  return Promise.reject(error);
});


export default request;



